.wrapItems {
    padding: 0px;
    margin-top: 15px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.wrapItems li {
    text-decoration: none;
    display: inline-block;
    margin: 0px 15px 15px 0px;
    color: white;
}

.panelItem {
    display: block;
    background: linear-gradient(to right, #ffdf71 35%, #ffd32f 85%);
    color: #ffffff;
    height: 45px;
}

.priceItem {
    float: right;
    padding-right: 15px;
    background: #ffd32f;
    font-size: 18px;
    line-height: 45px;
    font-weight: 600;
}
.platformItem {
    line-height: 35px;
    padding: 0px 15px;
    font-size: 25px;
}

@media (max-width: 650px) {
    .wrapItems li img {
        width: 160px;
        height: 90px;
    }
}